// base of imports
@import "./fonts";
@import "./colors";
@import "./mixins";

// root
:root {
    --btn-padding-y: 16px;
    --btn-padding-x: 32px;
}

// primary variables
$font-family: 'Graphik LC Web';
$font-size: 16px;

$steps: (
    auto: auto,
    4: 4px,
    8: 8px,
    12: 12px,
    16: 16px,
    20: 20px,
    24: 24px,
    28: 28px,
    32: 32px,
    36: 36px,
    40: 40px,
    44: 44px,
    48: 48px,
    52: 52px,
    56: 56px,
    60: 60px,
    64: 64px,
    68: 68px,
    72: 72px,
    76: 76px,
    80: 80px,
    84: 84px,
    88: 88px,
    92: 92px,
    96: 96px,
    100: 100px
);

$breakpoints: (
    "xxl": 1400px,
    "xl": 1280px,
    "lg": 1024px,
    "md": 768px,
    "sm": 576px,
    "xs": 0,
);

$font-weights: (
    "400": 400,
    "500": 500,
    "normal": 400,
    "medium": 500,
);