@font-face {
    font-family: 'Graphik LC Web';
    src: url('../../assets/fonts/GraphikLCWeb-Regular.eot');
    src: url('../../assets/fonts/GraphikLCWeb-Regular.eot?#iefix') format('embedded-opentype'),
        url('../../assets/fonts/GraphikLCWeb-Regular.woff2') format('woff2'),
        url('../../assets/fonts/GraphikLCWeb-Regular.woff') format('woff'),
        url('../../assets/fonts/GraphikLCWeb-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

